:export {
  themeFilter: invert(93%) hue-rotate(180deg);
  rightSidebarWidth: 302px;
}

.excalidraw .Toast {
  animation: fade-in 0.5s;
  background-color: var(--button-gray-1);
  border-radius: 4px;
  bottom: 10px;
  box-sizing: border-box;
  cursor: default;
  left: 50%;
  margin-left: -150px;
  padding: 4px 0;
  position: absolute;
  text-align: center;
  width: 300px;
  z-index: 999999;
}
.excalidraw .Toast .Toast__message {
  padding: 0 1.6rem;
  color: var(--popup-text-color);
  white-space: pre-wrap;
}
.excalidraw .Toast .close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.4rem;
}
.excalidraw .Toast .close .ToolIcon__icon {
  width: 1.2rem;
  height: 1.2rem;
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
