:export {
  themeFilter: invert(93%) hue-rotate(180deg);
  rightSidebarWidth: 302px;
}

.excalidraw {
  --userlist-hint-bg-color: var(--color-gray-10);
  --userlist-hint-heading-color: var(--color-gray-80);
  --userlist-hint-text-color: var(--color-gray-60);
  --userlist-collaborators-border-color: var(--color-gray-20);
}
.excalidraw .UserList {
  pointer-events: none;
  /*github corner*/
  padding: var(--space-factor) var(--space-factor) var(--space-factor) var(--space-factor);
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  gap: 0.625rem;
  box-sizing: border-box;
  max-height: 120px;
  max-width: 120px;
  overflow: hidden;
}
.excalidraw .UserList:empty {
  display: none;
}
.excalidraw .UserList > * {
  pointer-events: var(--ui-pointerEvents);
}
.excalidraw .UserList_mobile {
  padding: 0;
  justify-content: normal;
  margin: 0.5rem 0;
  max-width: none;
  max-height: none;
}
.excalidraw .UserList__more {
  width: 1.25rem;
  height: 1.25rem;
  position: relative;
  border-radius: 100%;
  outline-offset: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: 800;
  line-height: 1;
  color: var(--color-gray-90);
  flex: 0 0 auto;
  background-color: var(--color-gray-20);
  border: 0 !important;
  font-size: 0.5rem;
  font-weight: 400;
  flex-shrink: 0;
  color: var(--color-gray-100);
}
.excalidraw .UserList__more-img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
}
.excalidraw .UserList__more::before {
  content: "";
  position: absolute;
  top: -3px;
  right: -3px;
  bottom: -3px;
  left: -3px;
  border: 1px solid var(--avatar-border-color);
  border-radius: 100%;
}
.excalidraw .UserList__more--is-followed::before {
  border-color: var(--color-primary-hover);
}
.excalidraw .UserList__more--is-current-user {
  cursor: auto;
}
.excalidraw .UserList__collaborator-follow-status-icon {
  margin-left: auto;
  flex: 0 0 auto;
  width: 1rem;
  display: flex;
}
.excalidraw.theme--dark {
  --userlist-hint-bg-color: var(--color-gray-90);
  --userlist-hint-heading-color: var(--color-gray-30);
  --userlist-hint-text-color: var(--color-gray-40);
  --userlist-collaborators-border-color: var(--color-gray-80);
}
.excalidraw .UserList__collaborators {
  position: static;
  top: auto;
  margin-top: 0;
  max-height: 12rem;
  overflow-y: auto;
  padding: 0.25rem 0.5rem;
  border-top: 1px solid var(--userlist-collaborators-border-color);
  border-bottom: 1px solid var(--userlist-collaborators-border-color);
}
.excalidraw .UserList__collaborators__empty {
  color: var(--color-gray-60);
  font-size: 0.75rem;
  line-height: 150%;
  padding: 0.5rem 0;
}
.excalidraw .UserList__hint {
  padding: 0.5rem 0.75rem;
  overflow: hidden;
  text-align: center;
  color: var(--userlist-hint-text-color);
  font-size: 0.75rem;
  line-height: 150%;
}
.excalidraw .UserList__search-wrapper {
  position: relative;
  height: 2.5rem;
}
.excalidraw .UserList__search-wrapper svg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0.75rem;
  width: 1.25rem;
  height: 1.25rem;
  color: var(--color-gray-40);
  z-index: 1;
}
.excalidraw .UserList__search {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  border: 0 !important;
  border-radius: 0 !important;
  font-size: 0.875rem;
  padding-left: 2.5rem !important;
  padding-right: 0.75rem !important;
}
.excalidraw .UserList__search::placeholder {
  color: var(--color-gray-40);
}
.excalidraw .UserList__search:focus {
  box-shadow: none !important;
}
