:export {
  themeFilter: invert(93%) hue-rotate(180deg);
  rightSidebarWidth: 302px;
}

.excalidraw .Avatar {
  width: 1.25rem;
  height: 1.25rem;
  position: relative;
  border-radius: 100%;
  outline-offset: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: 800;
  line-height: 1;
  color: var(--color-gray-90);
  flex: 0 0 auto;
}
.excalidraw .Avatar-img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
}
.excalidraw .Avatar::before {
  content: "";
  position: absolute;
  top: -3px;
  right: -3px;
  bottom: -3px;
  left: -3px;
  border: 1px solid var(--avatar-border-color);
  border-radius: 100%;
}
.excalidraw .Avatar--is-followed::before {
  border-color: var(--color-primary-hover);
}
.excalidraw .Avatar--is-current-user {
  cursor: auto;
}
